<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link" href="index.html">  </a>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link" href="cart.html">  </a>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">   </a>
        </li>
      </ul>

      <h1 class="content__title"></h1>
      <span class="content__info">
        {{ $store.state.cartProducts.length }} 
      </span>
    </div>

    <section class="cart">
      <form
        class="cart__form form"
        action="#"
        method="POST"
        @submit.prevent="order"
      >
        <div class="cart__field">
          <div class="cart__data">
            <label class="form__label">
              <BaseFormText
                v-model="formData.name"
                :error="formError.name"
                title=""
                placeholder="   "
              />
            </label>

            <label class="form__label">
              <BaseFormText
                v-model="formData.address"
                :error="formError.address"
                title=" "
                name="address"
                placeholder="  "
              />
            </label>

            <label class="form__label">
              <BaseFormText
                v-model="formData.phone"
                :error="formError.phone"
                title=""
                type="tel"
                name="phone"
                placeholder="  "
              />
            </label>

            <label class="form__label">
              <BaseFormText
                v-model="formData.email"
                :error="formError.email"
                title="Email"
                type="email"
                name="email"
                placeholder="  Email"
              />
            </label>

            <label class="form__label">
              <BaseFormTextarea
                v-model="formData.comment"
                :error="formError.comment"
                title="  "
                placeholder=" "
              />
            </label>
          </div>

          <div class="cart__options">
            <h3 class="cart__title"></h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input
                    class="options__radio sr-only"
                    type="radio"
                    name="delivery"
                    value="0"
                    checked=""
                  />
                  <span class="options__value">
                     <b></b>
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input
                    class="options__radio sr-only"
                    type="radio"
                    name="delivery"
                    value="500"
                  />
                  <span class="options__value">  <b>500 </b> </span>
                </label>
              </li>
            </ul>

            <h3 class="cart__title"></h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input
                    class="options__radio sr-only"
                    type="radio"
                    name="pay"
                    value="card"
                  />
                  <span class="options__value">    </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input
                    class="options__radio sr-only"
                    type="radio"
                    name="pay"
                    value="cash"
                  />
                  <span class="options__value">    </span>
                </label>
              </li>
            </ul>
          </div>
        </div>

        <div class="cart__block">
          <ul
            class="cart__orders"
            v-for="item in products"
            :key="item.productId"
            :item="item"
          >
            <li class="cart__order">
              <h3>{{ item.product.title }}</h3>
              <b>{{ (item.amount * item.product.price) | numberFormat }} </b>
              <span>: {{ item.product.id }}</span>
            </li>
          </ul>

          <div class="cart__total">
            <p>: <b>500 </b></p>
            <p>
              : <b>{{ $store.state.cartProducts.length }} </b>  
               <b>{{ (totalPrice | numberFormat) + 500 }} </b>
            </p>
          </div>

          <button class="cart__button button button--primery" type="submit">
             
          </button>
        </div>
        <div class="cart__error form__error-block" v-if="formErrorMessage">
          <h4>  !</h4>
          <p>
            {{ formErrorMessage }}
          </p>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import BaseFormText from "@/components/BaseFormText.vue";
import BaseFormTextarea from "@/components/BaseFormTextarea.vue";
import numberFormat from "@/helpers/numberFormat.js";
import { mapGetters } from "vuex";
import axios from "axios";
import { API_BASE_URL } from "@/config.js";

export default {
  filters: { numberFormat },
  props: ["item"],
  components: {
    BaseFormText: BaseFormText,
    BaseFormTextarea: BaseFormTextarea,
  },
  computed: {
    ...mapGetters({
      products: "cartDetailProducts",
      totalPrice: "cartTotalPrice",
    }),
  },
  data() {
    return {
      formData: {},
      formError: {},
      formErrorMessage: "",
    };
  },
  methods: {
    order() {
      this.formError = {};
      this.formErrorMessage = "";
      axios
        .post(
          API_BASE_URL + "/api/orders",
          {
            ...this.formData,
          },
          {
            params: {
              userAccessKey: this.$store.state.userAccessKey,
            },
          }
        )
        .then((response) => {
          this.$store.commit("resetCart");
          this.$store.commit("updateOrderInfo", response.data);
          this.$route.push({ name: "orderInfo", id: response.data.id });
        })
        .catch((error) => {
          this.formError = error.response.data.error.request || {};
          this.formErrorMessage = error.response.data.error.message;
        });
    },
  },
};
</script>