<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link" href="index.html">  </a>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link" href="cart.html">  </a>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">   </a>
        </li>
      </ul>

      <h1 class="content__title">  <span> 23621</span></h1>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST">
        <div class="cart__field">
          <p class="cart__message">
             &nbsp;  . &nbsp;  
             &nbsp; .    &nbsp;
            &nbsp;     .
          </p>

          <ul class="dictionary">
            <li class="dictionary__item">
              <span class="dictionary__key">  </span>
              <span class="dictionary__value">
                {{ this.formData.name }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">   </span>
              <span class="dictionary__value">
                {{ this.formData.address }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">  </span>
              <span class="dictionary__value"> {{ this.formData.phone }} </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key"> Email </span>
              <span class="dictionary__value"> {{ this.formData.email }} </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">   </span>
              <span class="dictionary__value">    </span>
            </li>
          </ul>
        </div>

        <div class="cart__block">
          <ul
            class="cart__orders"
            v-for="item in products"
            :key="item.productId"
            :item="item"
          >
            <li class="cart__order">
              <h3>{{ item.product.title }}</h3>
              <b>{{ (item.amount * item.product.price) | numberFormat }} </b>
              <span>: {{ item.product.id }}</span>
            </li>
          </ul>

          <div class="cart__total">
            <p>: <b>500 </b></p>
            <p>
              : <b>3</b>   
              <b>{{ (totalPrice | numberFormat) + 500 }} </b>
            </p>
          </div>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import numberFormat from "@/helpers/numberFormat.js";

export default {
  filters: { numberFormat },
  props: ["item"],
  created() {
    if (
      this.$store.state.orderInfo &&
      this.$store.state.orderInfo.id === this.$route.params.id
    ) {
      return;
    }
    this.$store.dispatch("loadOrderInfo", this.$route.params.id);
  },
};
</script>